html {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
}

:root {
  --def-fs: 16px;
}

body {
  font-family: "Gilroy";
  font-size: var(--def-fs);
  -webkit-text-size-adjust: 100%;
  text-rendering: optimizeSpeed;
  line-height: 1.4;
  font-weight: 400;
  color: #242424;
}

a {
  color: #242424;
  text-decoration: none;
}

li {
  list-style-type: none;
}

h1 {
  font-weight: bold;
  font-size: 35px;
}

.search {
  position: relative;
}

.search__input {
  padding: 7px 30px;
  width: 100%;
  border: transparent solid 2px;
  border-radius: 10px;
  background-color: rgba(118, 118, 128, 0.12);
  background-image: url(../img/svg-icons/search-icon.svg);
  background-repeat: no-repeat;
  background-size: 15px;
  background-position: 10px 50%;
  outline: none;
}

.search__micro {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(-90%, -50%);
}

input[type="search" i]:focus {
  border: #F55F4B solid 2px;
}

.burger {
  position: relative;
  width: 30px;
  height: 23px;
  cursor: pointer;
  user-select: none;
  transition: 0.3s all ease-in;
}

.burger:hover .burger__line {
  width: 100%;
}

.burger__line,
.burger:after,
.burger:before {
  position: absolute;
  left: 0;
  height: 3px;
  background-color: #242424;
  border-radius: 999px;
  transition: 0.3s all;
}

.burger:after,
.burger:before {
  content: '';
  width: 100%;
  display: inline-block;
}

.burger:after {
  bottom: 0;
}

.burger::before {
  top: 0;
}

.burger__line {
  width: 60%;
  top: calc(50% - 2px);
  transition: 0.3s all;
}

.burger_toggle .burger__line {
  display: none;
}

.burger_toggle::after {
  transform: rotate(-45deg) translate(7px, -8px);
}

.burger_toggle::before {
  transform: rotate(45deg) translate(6px, 7px);
}

.btn-prime {
  padding: 12px 28px;
  font-weight: 700;
  font-size: 14px;
  color: #ffffff;
  background-color: #F55F4B;
  border: none;
  border-radius: 12px;
}

.btn-prime:hover {
  background-color: #BC1818;
}

.socials {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 20px;
  padding: 0;
}

.socials__item {
  justify-self: center;
  width: 50px;
  height: 50px;
  list-style-type: none;
  border-radius: 50%;
  background: #ffffff;
}

.socials__link {
  width: 100%;
  height: 100%;
  display: block;
}

.txt-bright {
  color: #F55F4B;
}

.modal {
  position: relative;
  opacity: 0;
  z-index: -99;
  transition: 0.3s;
  overflow: hidden;
}

.modal.is-visible {
  opacity: 1;
  z-index: 999;
  overflow: visible;
}

.modal.is-visible .modal__content {
  transform: translate(0);
}

.modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.41);
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.modal__content {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 90%;
  max-width: 980px;
  min-height: 50%;
  max-height: 90%;
  margin-left: auto;
  margin-right: auto;
  background: #ffffff;
  border-radius: 25px;
  box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.1);
  -webkit-overflow-scrolling: touch;
  transform: translate(-100%, 0);
  transition: transform 0.3s;
  overflow: auto;
}

.modal__head {
  position: sticky;
  top: 0;
  padding: 20px 35px;
  background: inherit;
}

.modal__title {
  margin: 0;
  font-weight: bold;
  font-size: 20px;
  text-align: center;
}

.modal__close {
  position: absolute;
  display: flex;
  top: 13px;
  right: 8px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: inherit;
  border-radius: 16px;
  width: 40px;
  height: 40px;
  padding: 13px 11px;
  transition: transform 0.3s;
  cursor: pointer;
}

.modal__close:active {
  transform: scale(1.05);
}

.modal__close::before,
.modal__close::after {
  content: "";
  display: block;
  position: relative;
  width: 100%;
  background: #c4c4c4;
  height: 2px;
  border-radius: 999px;
  transition: 0.3s;
}

.modal__close::before {
  transform: rotate(45deg);
  top: 1px;
}

.modal__close::after {
  transform: rotate(-45deg);
  bottom: 1px;
}

.modal__body {
  height: 100%;
  padding-bottom: 30px;
  padding-top: 10px;
  padding-left: 20px;
  padding-right: 20px;
}

.form__input {
  border-radius: 5px;
